<template>
	<br />
	<div id="nav">
		<img class="logo" src="#" alt="logo" />
		<div class="menu">
			<router-link class="menu-item" to="/">Home</router-link>
			<router-link class="menu-item" to="/product">Product</router-link>
			<router-link class="menu-item" to="/about">About us</router-link>
			<router-link class="menu-item" to="/contact">Contact</router-link>
		</div>
		<div class="menu2">
			<div class="search">
				<form class="searchForm" @submit.prevent>
					<input type="text" placeholder="Search" v-model="searchQuery" />
					<span v-show="searchQuery" class="removeInput" @click="removeSearchQuery">+</span>
				</form>
				<button class="btn-search" @click="submitSearch">Search</button>
			</div>
			<div class="login">
				<img class="icon" src="@/assets/icons/user.png" alt="user-icon" />
				<h4>Login</h4>
			</div>
			<img class="icon" src="@/assets/icons/cart.jpg" alt="cart-icon" />
		</div>
	</div>
</template>
<script>
export default {
	data() {
		return {
			isResult: false,
			searchQuery: "",
		};
	},

	methods: {
		removeSearchQuery() {
			this.searchQuery = "";
			this.isResult = false;
		},
		submitSearch() {
			var reqURL =
				"https://en.wikipedia.org/w/api.php?action=query&generator=search&gsrnamespace=0&exsentences=1&exintro&explaintext&exlimit=max&prop=extracts&gsrlimit=10&gsrsearch=" +
				this.searchQuery +
				"&format=json";

			this.$http.jsonp(reqURL).then(
				function(response) {
					this.wikiObj = response.data.query.pages;
					this.isResult = true;
				},
				function(response) {
					/* fail response msg */
					console.log(response);
				}
			);
		},
	},
};
</script>
