<template>
	<div class="container">
		<div class="filter">
			<span class="filter-item">
				<select @change="myfunction">
					<option disabled selected>Sort</option>
					<option value="Price: Low to High">Price: Low to High</option>
					<option value="Price: High to Low">Price: High to Low</option>
					<option value="Name: A - Z">Name: A - Z</option>
					<option value="Name: Z - A">Name: Z - A</option>
				</select>
			</span>
			<span class="filter-item">
				<input type="text" placeholder="Search" v-model="searchQuery" @keyup.enter="search" />
			</span>
			<span class="filter-item">
				<button @click="search">Search</button>
			</span>
		</div>

		<!-- first time, display all products -->
		<div class="row" v-show="inItData">
			<div class="block" v-for="product in products" :key="product.id">
				<img :src="product.image" :alt="'This is ' + product.name" />
				<h4>{{ product.name }}</h4>
				<div class="price" v-if="product.promo">
					<p class="price-promo">${{ format(product.price) }}</p>
					<p>${{ format(product.price_promo) }}</p>
				</div>
				<div class="price" v-else>
					<p>${{ format(product.price) }}</p>
				</div>
			</div>
		</div>

		<!-- after search, hide all products, only dispaly productSearchs -->
		<div class="row" v-show="!inItData">
			<div class="block" v-for="product in productSearchs" :key="product.id">
				<img :src="product.image" :alt="'This is ' + product.name" />
				<h4>{{ product.name }}</h4>
				<div class="price" v-if="product.promo">
					<p class="price-promo">${{ format(product.price) }}</p>
					<p>${{ format(product.price_promo) }}</p>
				</div>
				<div class="price" v-else>
					<p>${{ format(product.price) }}</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Product",

	props: {
		searchText: String,
	},
	data() {
		return {
			msg: "...",
			searchQuery: "",
			inItData: true,
			products: [
				{
					id: 1,
					name: "SamSung Galaxy S20+",
					image: require("@/assets/SamsungGalaxyS20+.png"),
					price: 1090,
					price_promo: 1000,
					promo: true,
				},
				{
					id: 2,
					name: "Iphone 12 Pro Max",
					image: require("@/assets/Iphone12ProMax.png"),
					price: 1500,
					price_promo: 1350,
					promo: true,
				},
				{
					id: 3,
					name: "Xiaomi Mi 11 5G",
					image: require("@/assets/XiaomiMi11_5G.png"),
					price: 890,
					price_promo: 800,
					promo: false,
				},
				{
					id: 4,
					name: "Oppo Find X3 Pro 5G",
					image: require("@/assets/OppoFindX3Pro5G.png"),
					price: 1250,
					price_promo: 1130,
					promo: true,
				},
				{
					id: 5,
					name: "Realme 8 Pro",
					image: require("@/assets/Realme8Pro.png"),
					price: 700,
					price_promo: 500,
					promo: false,
				},
				{
					id: 6,
					name: "Samsung Galaxy Fold",
					image: require("@/assets/SamsungGalaxyFold.png"),
					price: 2560,
					price_promo: 2300,
					promo: true,
				},
			],
			productSearchs: [],
		};
	},
	methods: {
		totalOfPromotionalProduct() {
			const promotionalProduct = this.products.filter((product) => product.promo);
			return promotionalProduct.length;
		},
		totalPriceOfProduct() {
			let sum = 0;
			this.products.forEach((product) => {
				sum += product.price;
			});
			return sum;
		},
		avgPriceOfProduct() {
			return (this.totalPriceOfProduct() / this.products.length).toFixed(2);
		},
		myfunction(event) {
			this.msg = event.target.value;
			if (event.target.value === "Name: A - Z") {
				this.products = this.sortByName(this.products, "ASC");
				this.productSearchs = this.sortByName(this.productSearchs, "ASC");
			}
			if (event.target.value === "Name: Z - A") {
				this.products = this.sortByName(this.products, "DESC");
				this.productSearchs = this.sortByName(this.productSearchs, "DESC");
			}
			if (event.target.value === "Price: Low to High") {
				this.products = this.sortByPrice(this.products, "ASC");
				this.productSearchs = this.sortByPrice(this.productSearchs, "ASC");
			}
			if (event.target.value === "Price: High to Low") {
				this.products = this.sortByPrice(this.products, "DESC");
				this.productSearchs = this.sortByPrice(this.productSearchs, "DESC");
			}
		},
		sortByName(obj, typeCompare) {
			if (typeCompare === "ASC") {
				obj.sort(function(a, b) {
					return a.name.localeCompare(b.name);
				});
				return obj;
			} else {
				obj.sort(function(a, b) {
					return b.name.localeCompare(a.name);
				});
				return obj;
			}
		},
		sortByPrice(obj, typeCompare) {
			if (typeCompare === "ASC") {
				obj.sort(function(a, b) {
					return a.price - b.price;
				});
				return obj;
			} else {
				obj.sort(function(a, b) {
					return b.price - a.price;
				});
				return obj;
			}
		},
		format(number) {
			number = new Intl.NumberFormat().format(number);
			return number;
		},
		search() {
			this.inItData = false;
			this.productSearchs = [];

			this.products.forEach((product) => {
				if (product.name.toLowerCase().includes(this.searchQuery.toLowerCase())) {
					this.productSearchs.push(product);
				}
			});

			if (this.searchQuery.length === 0) {
				this.productSearchs = this.products;
				this.results = false;
			}
		},
	},
	computed: {},
};
</script>

<style>
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	padding: 30px;
	color: #333;
}

.container {
	width: 80%;
	margin: auto;
	margin-bottom: 100px;
	margin-top: 30px;
}

.dropdown {
	display: flex;
	justify-content: center;
}

.dropdown-item {
	margin: 5px;
}

.row {
	margin-bottom: 50px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.block {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	width: 240px;
	height: 300px;
	background: #f2f2f2;
	border-radius: 5px;
	margin: 30px;
}

.block:hover {
	transform: scale(1.1);
	transition: 0.3s;
}

.block img {
	padding-top: 15px;
	width: 70%;
}

.price {
	display: flex;
}

.price p {
	margin: 0px 10px;
}

.price-promo {
	text-decoration: line-through;
	color: red;
}

.filter {
	display: flex;
	justify-content: center;
}

.filter-item {
	margin: 5px;
}

.filter-item input,
.filter-item select,
.filter-item button {
	height: 30px;
}
</style>
